.burger {
  display: none;
  background: #000000;
  height: 3px;
  width: 32px;
  position: relative;
  transition: all ease 0.1s;
  z-index: 9999;
}
.burger:before {
  background: #000000;
  height: 3px;
  width: 32px;
  display: block;
  position: absolute;
  content: "";
  left: 0;
  top: 7px;
  transition: all ease 0.2s;
}
.burger:after {
  background: #000000;
  height: 3px;
  width: 32px;
  display: block;
  position: absolute;
  content: "";
  left: 0;
  transition: all ease 0.2s;
  top: -7px;
}

